* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Lato", sans-serif;
  }
  
  html {
    background: #f7f3ec;
    background-image: url(https://s3.postimg.org/s1n3ji1ur/paper_fibers_2_X.png);
    box-sizing: border-box;
    font-family: "Lato", sans-serif;
    /* font-size: 14px;
    font-weight: 400; */
    
  }
  
 .navbar{
    max-width: 600px;
    margin: auto;
 }
 .navbar-container{
   margin-top: 40px;
 }
 .btn-nav{
     border: none;
     font-size: large;
     font-weight: bold;
     padding: 20px 40px;
     background-color: white;
 }
 .btn-nav:hover{
   border-bottom: 4px solid purple;
 }
 /* mainbody */
 .main-container{
   width: 1640px;
   margin: auto;
   margin-top: 40px;
  
 }
.round{
  border:1px solid rgb(66, 66, 66);
  border-radius: 100%;
  padding: 7.5px;
  margin: 5px;
}
.text-light{
  font-weight: 100;
  color: gray;
}
.read{
  text-transform: uppercase;
  font-weight: 550;
}
.title-name{
  margin-top: 30px;
 line-height: .5in;
 text-transform: uppercase;
}
.card{
  background-color: white;
  justify-content: center;
  padding: 20px;
}
img{
  width:370px;
  height: 280px;
 margin-top: 25px;
 padding: 10px;
}
/* fixing image at center */
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
 
}

.right-most{
  float: right;
}
/*  divide contents into equal distribution using grid */
.body-container{
  display: grid;
 grid-template-columns: 1fr 1fr 1fr;
 grid-gap: 80px;
}
.tsize{
  font-size: 64px;
  text-transform: capitalize;
}
.psize{
  font-size: 20px;
  font-weight: 100;
  color:gray;
  line-height: .35in;

}
